.app{
  height: 100vh;
  position: relative;
}

.container{
  position: absolute;
  bottom: 1%;
  left: 1%;
  right: 1%;
}

.column-3 {
  width: 75%;
  float: left;
}

.column-2 {
  width: 50%;
  float: left;
}

.column-1 {
  width: 25%;
  float: left;
}

button.calc-button {
  width: 100%;
  height: 100px;
  border: 2px solid #ddd;
  background-color: #eee;
  font-size: 32px;
  color: #555;
}

.calc-button:hover{
border:2px ridge rgb(202, 188, 188);
}

input.result {
  height: 100px;
  width: 100%;
  border: none;
  text-align: right;
  font-size: 32px;
  position: relative;
  max-width: 100vw;
  margin-right: 50px;
  float: right;
}

.previous-result {
  position: absolute;
  right: 50px;
  z-index: 99;
  top: 10px;
  color: #aaa;
}
